body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/starwars-api/static/media/starback.aad46eb2.jpg);
  background-repeat: repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'STARWARS HOLLOW';
  font-style: normal;
  font-weight: normal;
  src: local('STARWARS HOLLOW'), url(/starwars-api/static/media/Starjhol.9c91297e.ttf) format('truetype');
}

@font-face {
  font-family: 'STARWARS';
  font-style: normal;
  font-weight: normal;
  src: local('STARWARS'), url(/starwars-api/static/media/Starjedi.cc3019aa.ttf) format('truetype');
}

h1 {
  font-family: 'STARWARS HOLLOW';
  font-weight: bold;
  color: yellow;
}

nav a {
  text-decoration: none;
  font-family: 'STARWARS';
  color: yellow;
}

.swcard {
  width: 20em;
  height: 20em;
  overflow-y: scroll;
  overflow-wrap: break-word;
}
